
import {State} from "./state";
import {HttpClient} from "@angular/common/http";
import {ToastrService} from "ngx-toastr";
import {Product} from "../models/product";
import {firstValueFrom} from "rxjs";
import {ResponseDto} from "../models/responsiveHelper/responseDto";
import {environment} from "../environments/environment";
import { Injectable } from "@angular/core";

@Injectable({
  providedIn: 'root',
})
export class ProductService {
  private products: Product[] = [];

  constructor(
    private state: State,
    private http: HttpClient,
    private toastr: ToastrService,
  ) {
  }


  async getAllProducts(): Promise<Product[]> {
    try {
      const res: any = await firstValueFrom(
        this.http.get<ResponseDto<Product[]>>(`${environment.BASE_URL}/food/order/product`)
      );
      this.state.setProducts(res.responseData);
      return res.responseData;
    } catch (error) {
      console.error(error);
      this.toastr.error('Failed to fetch products. Please try again later.');
      throw error;
    }
  }

  async getAllProductsForSelectedCategory(categoryId: number): Promise<Product[]> {
    try {
      const res: any = await firstValueFrom(
        this.http.get<ResponseDto<Product[]>>(`${environment.BASE_URL}/food/order/category/${categoryId}/product`)
      );

      this.state.setProducts(res.responseData);

      return res.responseData;
    } catch (error) {
      this.toastr.error('Failed to fetch products for the selected category. Please try again later.');
      throw error;
    }
  }

  async deleteProductById(id: number | undefined){
    try {
      const url = `${environment.BASE_URL}/food/order/${id}`;

      await firstValueFrom(this.http.delete(url));

      this.products = this.products.filter(p => p.id !== id);
      const updatedProducts = this.http.get<Product[]>('api/products');
      return updatedProducts;

      const toast = this.toastr.success('The product was successfully deleted!');
    } catch (error) {
      this.toastr.error('Failed to delete the product. Please try again later.');
      throw error;
    }
  }


}
