import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import {State} from "./state";
import {Category} from "../models/category";
import {firstValueFrom} from "rxjs";
import {ResponseDto} from "../models/responsiveHelper/responseDto";
import {environment} from "../environments/environment";

@Injectable({
  providedIn: 'root',
})
export class CategoryService {
  private categories: Category[] = [];

  constructor(
    private state: State,
    private http: HttpClient,
    private toastr: ToastrService,
  ) {}

  async getAllCategories(): Promise<Category[]> {
    try {
      const res: any = await firstValueFrom(
          this.http.get<ResponseDto<Category[]>>(`${environment.BASE_URL}/food/order/category`)
      );

      this.state.setCategories(res.responseData);
      return res.responseData;
    } catch (error) {
      this.toastr.error('Failed to fetch categories. Please try again later.');
      throw error;
    }

  }
  async deleteCategoryById(categoryId: number | undefined): Promise<void> {
    try {
      if (categoryId=== undefined) {
        throw new Error('Invalid category ID');
      }

      const url = `${environment.BASE_URL}/food/order/${categoryId}`;
      await firstValueFrom(this.http.delete(url));

      this.categories = this.categories.filter( c => c.categoryId !== categoryId);

      const toast = this.toastr.success('The category was successfully deleted!');
    } catch (error) {
      this.toastr.error('Failed to delete the category. Please try again later.');
      throw error;
    }
  }








}
