import {Component, OnInit} from '@angular/core';
import {SharedProductCategoryService} from "../../../services/shared/user-Pages.Service/shared-prod_cat.service";
import { Category } from 'src/app/models/category';
import { Product } from 'src/app/models/product';
import { DatePipe } from '@angular/common';
import { ProductService } from 'src/app/services/productService';
import { CategoryService } from 'src/app/services/categoryService';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',

})

export class MainComponent implements OnInit {
  categories: Category[] = [];
  products: Product[] = [];
  displayType: 'products' | 'categories' = 'categories';

  constructor(
    private sharedService: SharedProductCategoryService,
    private productService: ProductService,
    private categoryService: CategoryService
  ) {
  }


  getProductSpecificProperty(product: Product): string {
    return `${product.name} - ${product.description}
    - ${product.price}- ${product.quantity}`;
  }

  getCategorySpecificProperty(category: Category): string {
    return `${category.categoryName} - ${category.categoryImageUrl}`;
  }

  isProduct(item: Category | Product): item is Product {
    const product = item as Product;
    return product?.name !== undefined && product?.description !== undefined &&
      product?.price !== undefined && product?.quantity !== undefined;
  }

  isCategory(item: Category | Product): item is Category {
    const category = item as Category;
    return category.categoryName !== undefined && category?.categoryImageUrl !== undefined;
  }

  getTableHeaders(): string[] {
    if (this.displayType === 'categories') {
      return ['Category Name', 'Image', 'Action'];
    } else if (this.displayType === 'products') {
      return ['Product Name', 'Description', 'Price', 'Quantity', 'Action'];
    }

    return [];
  }

  getTableProperties(item: Category | Product): (string | number)[] {
    if (this.isProduct(item)) {
      const product = item as Product;
      return Object.values(product);
    } else if (this.isCategory(item)) {
      const category = item as Category;
      return Object.values(category);
    }

    return [];
  }

  switchDisplay(type: 'products' | 'categories') {
    this.displayType = type;
    this.loadData();
  }

  async loadData() {
    try {
      if (this.displayType === 'categories') {
        await this.sharedService.loadAllCategories();
        this.categories = this.sharedService.getCategories();

      } else if (this.displayType === 'products') {
        await this.sharedService.loadAllProducts();
        this.products = this.sharedService.getProducts();
      }
    } catch (error) {
      console.error('Failed to load data', error);
    }
  }

  getData(): (Category | Product)[] {
    return this.displayType === 'categories' ? this.categories : this.products;
  }

  ngOnInit(): void {
    this.loadData();
  }

  isImageUrl(url: string): boolean {
    return url.endsWith('.jpg') || url.endsWith('.png');
  }

  deleteItem(item: any) {

  }
}
